import React, { Component } from 'react';
import './css/header.css';
import logo from './images/provendor.png';
import '../../node_modules/bootstrap/dist/css/bootstrap.min.css';

export default class Header extends Component {
    render() {
        return (
            <div class="header-div">
                <div className="container">
                    <div class="row spacer25"></div>
                    <div className="row">
                        <div className="col-md-4 logostyle">
                            <img src={logo} ></img>
                        </div>
                        <div className="col-md-8">
                            <div className="row">
                                <div className="col-md-3">

                                </div>
                                <div className="col-md-3">
                                    <button class="header-button btn btn-primary">Today's Deals</button>
                                </div>
                                <div className="col-md-3">
                                    <button class="header-button btn btn-primary ">Sell</button>
                                </div>
                                <div className="col-md-3">
                                    <button class="header-button btn btn-primary">Login</button>
                                </div>
                            </div>
                            <div class="row spacer25"></div>
                            <div className="row">
                                <div className="col-md-3">
                                <button class="header-button btn btn-primary">Sell</button>
                                </div>
                                <div className="col-md-6">
                                    <input class="header-search-bar" type="text" placeholder="Search"></input>
                                </div>
                                <div className="col-md-3">
                                    <button class="header-button btn btn-primary">Cart</button>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="row spacer25"></div>
                </div>
            </div>
        );
    }
}


