import React, { Component } from 'react';
import './css/footer.css';
import { Map, InfoWindow, Marker, GoogleApiWrapper } from 'google-maps-react';

class Footer extends Component {

    constructor(props) {
        super(props);
        this.state = {
            showingInfoWindow: false,
            activeMarker: {},
            selectedPlace: {},
        }
        this.onMapClicked = this.onMapClicked.bind(this);
        this.onMarkerClick = this.onMarkerClick.bind(this);
        this.onInfoWindowClose = this.onInfoWindowClose.bind(this);
    }

    onMarkerClick(props, marker, e) {
        this.setState({
            selectedPlace: props,
            activeMarker: marker,
            showingInfoWindow: true
        });
    }

    onMapClicked(props) {
        if (this.state.showingInfoWindow) {
            this.setState({
                showingInfoWindow: false,
                activeMarker: null
            })
        }
    }
    onInfoWindowClose() {

    }

    render() {
        const style = {
            width: '40vw',
            height: '40vh',
            right: '10px',
            height: '53vh',
            display: 'block',
        }

        return (

            <div className="footer-inline-style main-bg-view" >
                <div className="contactborder row">
                    <div className="col-sm-7">
                        <div className="contact"> Contact Us </div>
                        <div className="container">
                            <div className="row">
                                <div className="col-md-1">
                                </div>
                                <div className="col-md-5">
                                    <input type="text" className="text-input-footer" placeholder="Name *" />
                                </div>
                                <div className="col-md-5">
                                    <input type="text" className="text-input-footer" placeholder="Email *" />
                                </div>
                                <div className="col-md-1">
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-1">
                                </div>
                                <div className="col-md-10">
                                    <input type="text" className="text-input-footer" placeholder="Subject *" />
                                </div>
                                <div className="col-md-1">
                                </div>
                            </div>
                            <div className="row spacer25"></div>
                            <div className="row">
                                <div className="col-md-1">
                                </div>
                                <div className="col-md-3">
                                    <label className="contact-label">PROVENDOR</label>
                                </div>
                                <div className="col-md-4">
                                    <label className="contact-label">Website :</label>
                                    <a href="#">www.provendor.com</a>
                                </div>
                                <div className="col-md-4">
                                    <label className="contact-label">Email :</label>
                                    <a href="#">info@provendor.com</a>
                                </div>
                                <div className="col-md-2">
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-12 text-center">
                                    <button className="contact-btn viewmore-btn">SEND</button>
                                </div>
                            </div>
                            <div className="row spacer25"></div>
                            <div className="row">
                                <div className="col-md-1">
                                </div>
                                <div className="col-md-5 app-links">
                                    <label class="contact-label">Download <span>Provendor</span> Mobile App</label>
                                    <ul class="app_bx">
                                        <li>
                                            <a href="#" target="_blank">
                                                <img src="https://static.grapes.digital/downloads/ignis/google-play.png"
                                                    alt="Google Play"></img>
                                            </a>
                                        </li>
                                    </ul>
                                </div>
                                <div class="col-md-6">
                                    <div class="social_links">
                                        <label class="contact-label"> Follow us on</label>
                                        <div className="row">
                                            <a class="col-md-1 fb fa fa-facebook" href="#" target="_blank"></a>
                                            <a class="col-md-1 tw fa fa-twitter" href="#" target="_blank"></a>
                                            <a class="col-md-1 fa fa-youtube" href="#" target="_blank"></a>
                                            <a class="col-md-1 fa fa-google-plus" href="#" target="_blank"></a>
                                            <a class="col-md-1 fa fa-instagram" href="#" target="_blank"></a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-sm-5">
                        <Map google={this.props.google} mapType={'map'} onClick={this.onMapClicked} centerAroundCurrentLocation={false} style={style} initialCenter={{ lat: 53.2734, lng: -7.778320310000026 }} zoom={13}>
                            <Marker onClick={this.onMarkerClick} name={'Provendor'} />
                            <InfoWindow
                                marker={this.state.activeMarker}
                                visible={this.state.showingInfoWindow}>
                                <div>
                                    <h2>{this.state.selectedPlace.name}</h2>
                                </div>
                            </InfoWindow>
                        </Map>
                    </div>
                </div>
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-md-1"></div>
                        <div className="col-md-5">
                            <label className="contact-label-sm">Powered by Provendor @2018</label>
                        </div>
                        <div className="col-md-6">
                            <div className="row">
                                <div className="col-md-12 fl-right">
                                    <a href="/terms-of-use.html">Terms of use</a>|
                                    <a href="/privacy-policy.html">Privacy Policy</a>|
                                    <a href="/contact-us.html">Contact us</a>|
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
    );
    }
}

export default GoogleApiWrapper({
    apiKey: 'AIzaSyBAa_jlC8KHxiF_yBjDXKXE_Pscspt6XQA',
})(Footer);



